
.hamburger {
  display: inline-block;
  cursor: pointer;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: 0.38s;
  transition: 0.38s;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
.hamburger.is-active {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
